@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
.index {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    
}

.team {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

    color: white;
    text-align: center;
    font-size: 60px;
    padding: 30px;
    margin: 20px;
    border-radius: 20px;

}
.count-display {
    font-size: 130px;
    margin: 25px;
}
.team button {
    background-color: transparent;
    font-size: 40px;
    color: white;
    border: 3px solid white;
    border-radius: 10px;
    width: 70px;
    height: 70px;
    margin: 15px;
}
.team button:active {
    box-shadow: inset 1px 1px 10px #373737;
 }
.points-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.points-display {
    font-size: 250px;
    text-shadow: 1px 1px 4px gray;
}
.points-display button {
    border: none;
    background-color: transparent;
    font-size: 30px;
    transition: -webkit-transform 200ms;
    transition: transform 200ms;
    transition: transform 200ms, -webkit-transform 200ms;
}
.points-display button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.edit-points {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}
.buttons {
    display: -webkit-flex;
    display: flex;
}
.edit-points button {
    border: none;
    background-color: transparent;
    color: #282828;
    font-size: 30px;
    transition: -webkit-transform 200ms;
    transition: transform 200ms;
    transition: transform 200ms, -webkit-transform 200ms;
}
.edit-points input {
    font-size: 30px;
    text-align: center;
    padding: 12px 20px;
    margin: 10px;
    border: none;
    border: 3px solid #282828;
}
.edit-points button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

/*remove arrows from number spinner input */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.counter-wrapper {
    font-family: 'Open Sans', sans-serif;
    color: #282828;
    text-transform: uppercase;
    text-align: center;
    font-weight: bold;
    width: 90vw;
    max-width: 90vw;
}
.teams {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

